import React from 'react';
import { useEffect, useState } from 'react';
import { getAuth, signOut } from "firebase/auth";
import {
    getDocs,
    getDoc,
    collection,
    addDoc,
    deleteDoc,
    updateDoc,
    doc, orderBy, query
  } from "firebase/firestore";
  import { db, auth } from '../../utils/firebase.utils';
  import { useNavigate } from "react-router-dom";
  import { Container, Row, Col, Button, Spinner} from 'react-bootstrap';
  import { Link } from 'react-router-dom';
  import './dashboard.css'


export default function Dashboard() {
    const uid = sessionStorage.getItem("uid");
    const [displayName, setDisplayName] = useState("");
    const [loading, setLoading] = useState(true);
    const [userEmail, setUserEmail] = useState(true);
    const [loggingOut, setLoggingOut] = useState(false);
    const navigate = useNavigate();
    const fetchUserDetails = async () =>{
      const user_Email = sessionStorage.getItem('user-email')
      if(!user_Email){
        navigate('/')
      }
      setUserEmail(user_Email)
      setLoading(false)
    }
    useEffect(()=>{
      setTimeout( fetchUserDetails, 1000);
    }, [])
    const handleCreateInvoice = (e) => {
        e.preventDefault();
        navigate('/create')
    } 
    const handleRetrieveInvoice = (e) => {
        e.preventDefault();
        navigate('/invoice-table')
    }
    const auth = getAuth();

    function handleSignOut() {
      setLoggingOut(true)
      sessionStorage.removeItem('user-email')
      signOut(auth)
        .then(() => {
          // Sign-out successful.
          setLoggingOut(false);
         alert('Sign out succesful')
         navigate('/')
        })
        .catch((error) => {
          // An error happened.
        });
    }

  return loading
  ?
    <Container>
        <Row className='justify-content-center mt-5'>
            <Col xs={1}>
              <Spinner animation="border" role="status">
                <span className="visually-hidden">Loading...</span>
              </Spinner>
            </Col>
        </Row>
    </Container>
  :
  <div>
        <Container className='mt-3'>
          <Row className='justify-content-between'>
            <Col sm = '2' xs = '2' md ='1'>
                <Link to = '/' style={{textDecoration : 'none'}} className='dashboard-home'>Home</Link>
            </Col>
            <Col sm = '2' xs = '2' md = '1'>
              <p className='dashboard-signout' onClick={handleSignOut} >Sign Out</p>
            </Col>
          </Row>
          <Row className='justify-content-center mt-5'>
            <Col xs ='auto'><p>Welcome, {userEmail}</p></Col>
          </Row>
          <Row className='justify-content-center mt-5'>
            <Col xs ='auto'><Button type='button' onClick={handleCreateInvoice}>Create Invoice</Button></Col>
            <Col xs ='auto'><Button type='button' onClick={handleRetrieveInvoice}>Retrieve Invoice</Button></Col>

          </Row>
         { loggingOut ? <Row className='justify-content-center mt-5'>
            <Col xs={1}>
              <Spinner animation="border" role="status">
                <span className="visually-hidden">Loading...</span>
              </Spinner>
            </Col>
          </Row>:
          <div></div>}
        </Container>
    </div> 


}
