import React from 'react';
import { useState } from 'react';
import { useNavigate, Link } from "react-router-dom";
import { createInvoiceDocumentFromAuth } from '../../utils/firebase.utils';
import { getDoc } from 'firebase/firestore';
import { Form, Container, Row, Col, Button, Spinner} from 'react-bootstrap';
import { Table } from 'react-bootstrap';
import { v4 as uuid } from 'uuid';


const defaultCustomerDetails = {
    cName : '',
    cAddress: '',
    cEmail: '',
  };
const defaultItemDetails = {
    itemName : '',
    itemCost: '',
    itemQuantity: '',
    itemDescription : ''
  };
const defaultItems = [];
export default function CreateInvoice() {
    const [customerFields, setCustomerFields] = useState(defaultCustomerDetails);
    const { cName, cAddress, cEmail} = customerFields;
    const [itemFields, setItemFields] = useState(defaultItemDetails);
    const { itemName, itemCost, itemQuantity, itemDescription} = itemFields;
    const uid = sessionStorage.getItem("uid");
    const [items, setItems] = useState(defaultItems);
    const [generatingInvoice, setGeneratingInvoice] = useState(false);

    const navigate = useNavigate();
    const resetFormFields = () => {
        setItemFields(defaultItemDetails);
      };
    const handleChangeCustomers = (event) => {
        const { name, value } = event.target;
        setCustomerFields({ ...customerFields, [name]: value });
      };
      const handleChangeItem = (event) => {
        const { name, value } = event.target;
        setItemFields({ ...itemFields, [name]: value });
      };

    const createInvoiceId = () => {
      // Generate a random number between 0 and 99999
      const randomNumber = Math.floor(Math.random() * 100000);
    // Convert the random number to a string and pad it with leading zeros
      const paddedNumber = randomNumber.toString().padStart(5, '0');
    // Combine the "RAD5" string with the padded random number to create the invoice ID
      const invoiceId = `RAD5-${paddedNumber}`;
    return invoiceId;
    }

    const handleInvoiceClick = async (event) => {
        setGeneratingInvoice(true)
        event.preventDefault();
        //Get invoice id and add to sessionStorage
        const invoiceId = createInvoiceId();
        sessionStorage.setItem("invoiceId", invoiceId);
        //Add Invoice and Item to an Invoice collection and item subcollection
        const invoiceDocRef = await createInvoiceDocumentFromAuth(uid, customerFields, items, invoiceId);

        //Invoice Document and add to sessionStorage
        const documentSnapshot = await getDoc(invoiceDocRef);
        const documentData = documentSnapshot.data();
        const documentDataString = JSON.stringify(documentData);
        sessionStorage.setItem("invoiceData", documentDataString);

        const itemsString = JSON.stringify(items);
        sessionStorage.setItem('items', itemsString)
        setGeneratingInvoice(false)
        navigate('/invoice')
      };

      const addItem = (e) =>{
        e.preventDefault();
        setItems([...items, {...itemFields}]);
        resetFormFields();
      }
    
  return (
    <div>
      <Container className=''>
        <Row className='justify-content-between mt-3 mb-5'>
                <Col sm = '2' xs = '2' md ='1'>
                  <Link to = '/' style={{textDecoration : 'none'}}>Home</Link>
                    </Col>
                    <Col sm = '2' xs = '2' md = '1'>
                      Sign Out
                </Col>
        </Row>    
       <Row>
            <Col>
              <Form>
                <Form.Group className="mb-3" controlId="cEmail">
                  <Form.Control type="email" name="cEmail" placeholder="Customer Email" value = {cEmail} onChange={handleChangeCustomers} />
                </Form.Group>

                <Form.Group className="mb-3" controlId="cName">
                  <Form.Control type="text" name="cName" placeholder="Customer Name" required value = {cName} onChange={handleChangeCustomers} />
                </Form.Group>

                <Form.Group className="mb-3" controlId="cAddress">
                  <Form.Control type="text" name="cAddress" placeholder="Customer Address" value = {cAddress} onChange={handleChangeCustomers} />
                </Form.Group>
              </Form>
            </Col>
            <Col>
              <Form>
                <Form.Group className="mb-3" controlId="itemName">
                  <Form.Control type="text" name="itemName" placeholder="Item Name" value = {itemName} onChange={handleChangeItem} />
                </Form.Group>

                <Form.Group className="mb-3" controlId="itemCost">
                  <Form.Control type="number" name="itemCost" placeholder="Item Cost" value = {itemCost} onChange={handleChangeItem} />
                </Form.Group>

                <Form.Group className="mb-3" controlId="itemQuantity">
                  <Form.Control type="number" name="itemQuantity" placeholder="Item Quantity" value = {itemQuantity} onChange={handleChangeItem} />
                </Form.Group>

                <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                  <Form.Control as="textarea" rows={3}  placeholder='Item Description' name='itemDescription' value = {itemDescription} onChange={handleChangeItem}/>
                </Form.Group>
                <Button type='button' onClick={addItem}>Add Item</Button>
              </Form>
            </Col>
        </Row>
        <Row>
          <Col>
            <Table striped bordered hover>
              <thead>
                <tr>
                  <th>Item</th>
                  <th>Description</th>
                  <th>Quantity</th>
                  <th>Price</th>
                  <th>Total</th>
                </tr>
              </thead>
              <tbody>
                {
                  items.map(item => {
                   return (
                    <tr key={uuid()}>
                    <td>{item.itemName}</td>
                    <td>{item.itemDescription}</td>
                    <td>{item.itemQuantity}</td>
                    <td>{item.itemCost}</td>
                    <td>{item.itemQuantity * item.itemCost}</td>
                </tr>
                   )
                  })
                }
                
              </tbody>
              <tfoot>
              <tr>
                <td></td>
                <td></td>
                <td></td>
                  <td>Total Amount</td>
                  <td>{items.reduce((accumulator, currentElement) =>{
                        return accumulator + currentElement.itemQuantity * currentElement.itemCost;
                  }, 0)}</td>
                </tr>
              </tfoot>
            </Table>
            <Button type='button' onClick={handleInvoiceClick}>Generate Invoice</Button>
          </Col>
        </Row>
        { generatingInvoice ? <Row className='justify-content-center mt-5'>
            <Col xs={1}>
              <Spinner animation="border" role="status">
                <span className="visually-hidden">Loading...</span>
              </Spinner>
            </Col>
          </Row>:
          <div></div>}
      </Container>
        

      

    </div>
  )
}
