import React from 'react';
import { useState } from 'react';
import { signInAuthUserWithEmailAndPassword } from '../utils/firebase.utils';
import { useNavigate } from "react-router-dom";
import { Container, Row, Col, Table, Button, Spinner } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';



const defaultFormFields = {
    email: '',
    password: '',
  };

export default function SignIn() {
    const [formFields, setFormFields] = useState(defaultFormFields);
    const { email, password } = formFields;
    const [loggingIn, setLoggingIn] = useState(false);

    const navigate = useNavigate();
    const resetFormFields = () => {
        setFormFields(defaultFormFields);
      };
    const handleChange = (event) => {
        const { name, value } = event.target;
        setFormFields({ ...formFields, [name]: value });
      };

    const handleSubmit = async (event) => {
        event.preventDefault();
        setLoggingIn(true)
        try {
          const response = await signInAuthUserWithEmailAndPassword(
            email,
            password
          );
          console.log(response);
          resetFormFields();
          sessionStorage.setItem("uid" , response.user.uid);
          sessionStorage.setItem("user-email" , response.user.email);
          setLoggingIn(false);
          navigate("/dash");
          resetFormFields();
        } catch (error) {
          switch (error.code) {
            case 'auth/wrong-password':
              alert('incorrect password for email');
              setLoggingIn(false);
              break;
            case 'auth/user-not-found':
              setLoggingIn(false);
              alert('no user associated with this email');
              break;
            default:
              console.log(error);
              setLoggingIn(false);
          }
        }
      };
    
  return (
    <div>
      <Container className='mt-5'>
        <Row className='justify-content-center'>
          <Col md = '6' lg ='4'>
            <h5>Sign in and create invoices for your customers</h5>
            <Form onSubmit={handleSubmit}>
              <Form.Group className="mb-3" controlId="email">
                <Form.Label>Email address</Form.Label>
                <Form.Control type="email" name="email" placeholder="Enter email" required value = {email} onChange={handleChange} />
                <Form.Text className="text-muted">
                  We'll never share your email with anyone else.
                </Form.Text>
              </Form.Group>
              <Form.Group className="mb-3" controlId="password">
                <Form.Label>Password</Form.Label>
                <Form.Control type="password" placeholder="Password" name="password" required value = {password} onChange={handleChange} />
              </Form.Group>
              <Button variant="primary" type="submit">
                    Sign in
              </Button>
            </Form>
          </Col>
        </Row>
        <Row className='justify-content-center mt-3'>
          <Col lg = '4' sm = '6'>
          {loggingIn
          ?
          <Spinner animation="border" role="status">
                <span className="visually-hidden">Loading...</span>
          </Spinner>
          :
          <div></div>}
          </Col>
        </Row>
      </Container>

       {/* <form onSubmit={handleSubmit}>
        <label htmlFor="email"></label>
        <input type="email" name="email" id="email" placeholder="Email" required value = {email} onChange={handleChange} />
        <label htmlFor="password"></label>
        <input type="password" name="password" id="password" placeholder="Password" required value = {password} onChange={handleChange}/>
        <button>Sign in</button>
      </form> */}
    </div>
  )
}
