import React, { useEffect } from 'react'
import { Container, Row, Col, Table, Spinner } from 'react-bootstrap';
import { getFirestore, doc, getDoc, setDoc, collection, addDoc, getDocs, query, where } from 'firebase/firestore';
import { db } from '../utils/firebase.utils';
import { useState} from 'react';
import { v4 as uuid } from 'uuid';
import { useNavigate } from "react-router-dom";
import { Eye } from 'react-bootstrap-icons';
import { Button } from "react-bootstrap";



export default function InvoiceTable() {
  const [myInvoices, setMyInvoices] = useState([]);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();
    const getInvoices = async () => {
      let invoices = []
        const invoiceCollectionRef = collection(db, 'invoice');
        const querySnapshot = await getDocs(invoiceCollectionRef);
        querySnapshot.forEach((doc) => {
           invoices.push(doc.data())
        });
        setMyInvoices(invoices);
        setLoading(false)
    }
    const viewInvoice = async (e, invoiceId) =>{
      e.preventDefault();
      sessionStorage.setItem('invoiceId', invoiceId)      
      // navigate('/view-invoice')
      // navigate('/view-invoice', { target: '_blank' });
    window.open('/invoice-app/view-invoice', '_blank');

    }
    useEffect(() => {
        getInvoices();
    }, [])

  return loading ?
      <Container>
      <Row className='justify-content-center mt-5'>
          <Col xs={1}>
            <Spinner animation="border" role="status">
              <span className="visually-hidden">Loading...</span>
            </Spinner>
          </Col>
      </Row>
    </Container>
  :
  <Container>
        <Row>
            <Col>
            <Table striped bordered hover>
            <thead>
              <tr>
                <th>Date</th>
                <th>Customer</th>
                <th>Action</th>
              </tr>
            </thead>
            <tbody>
            {
                  myInvoices.map(invoice => {
                   return (
                    <tr key={uuid()}>
                    <td>{invoice.invoiceId}</td>
                    <td>{invoice.cName}</td>
                    <td>
                        <Eye onClick={(e) => {viewInvoice(e,invoice.invoiceId)}} style={{cursor:'pointer'}} />
                      {/* </Button> */}
                      </td>
                </tr>
                   )
                  })
                }
             
            </tbody>
            <tfoot>
            <tr>
              <td></td>
              <td></td>
              <td></td>
              </tr>
            </tfoot>
          </Table>
            </Col>
        </Row>
    </Container>
}
